.services-container {
    display: flex;
    justify-content: center; /* Center content horizontally */
    align-items: center; /* Center content vertically */
    height: 100vh; /* height as per your requirement */
    margin: 100px;
    overflow: hidden;
  }
  
  .services-header {
    width: 900px;
    padding: 20px;
  }
  
  .services-header h3 {
    display: flex;
    align-items: flex-start;
    color: #f85e9f;
    font-family: Nunito Sans;
    font-size: 23px;
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
    letter-spacing: 4.6px;
    text-transform: uppercase;
  }
  
  .services-header p {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    align-self: stretch;
    color: #191825;
    font-family: Nunito Sans;
    font-size: 44px;
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
  }
  
  .services-list {
    display: flex;
    align-items: flex-start;
    gap: 21px;
    overflow-x: auto;
    padding-bottom: 10px; 
  }
  
  .services-list::-webkit-scrollbar {
    display: none; /* Hid scrollbar */
  }
  
  .service-item {
    margin: 30px 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; /* Center the content vertically */
    gap: 10px;
    min-width: 270px;
    max-width: 350px;
    min-height: 350px; /* Increase the height*/
    padding: 20px;
    border-radius: 32px;
    box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.10),
      0px 12px 20px 0px rgba(0, 0, 0, 0.10);
  }
  
  .service-item img {
    width: 64px;
    height: 64px;
  }
  
  .service-item h4 {
    display: flex;
    flex-direction: column;
    align-self: stretch;
    color: #191825;
    text-align: center;
    font-family: Nunito Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
  }
  
  .service-item p {
    display: flex;
    flex-direction: column;
    align-self: stretch;
    color: rgba(25, 24, 37, 0.50);
    text-align: center;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
  }
  
